import React from 'react'
export default class Spinner extends React.Component {
  static defaultProps = {
    size: 20,
  }
  render(){

    return(
      <span style={{width: this.props.size, height: this.props.size, display: 'inline-block'}}>
        <img style={{width: '100%'}} src="data:image/gif;base64,"/>
      </span>
    )
  }
}



